.hero {
    width: 86vw;
    height: 43vh;
    background-image: url(/Portfolio/static/media/bg.0e875127.jpg);
    background-size: cover;
    position: relative;
    float: right;
   
  }
  
  .hero-container{
    font-family: 'Circular Std', sans-serif;font-family: 'Circular Std', sans-serif;
    position: absolute;
    bottom: 0;
    left: 0;
    margin-left: 5rem;
    margin-top: 9rem;
    font-size: 3rem;
    color: white;
    font-weight:900;
    margin-bottom: 3rem;
  }

  .gradient {
    content: "";
    display: block;
    height: 20%;
    width: 100%;
    position: absolute;
    bottom: 0;
    background: linear-gradient(to bottom, transparent, #121212); 
  }

  .play {
    margin: 0;
    margin-right: 1rem;
    cursor: pointer;
  
  }

  .follow {
    cursor: pointer;
    
  }

  .artist {
    margin-left: 4px;
  }

  h1 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .animation-hero {
    -webkit-animation: fadeInAnimation ease 3s;
            animation: fadeInAnimation ease 3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  @-webkit-keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}

  @keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}
.header {
